body {
  background: #FFF;
  line-height: 1.4;
}

.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.form-wrapper {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  padding: 40px 90px 40px 67px;
}

.block {
  display: flex;
}

.footer {
  height: 180px;
  background-color: #000;
}

.form {
  max-width: 665px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  width: 100%;
  padding: 20px;
  border: 3px solid #c7a221;
  border-radius: 5px;
}

.form__title {
  text-align: center;
  text-shadow: 0 3px 11px #000000;
  color: #ffffff;
  font-family: "Circle";
  font-size: 53px;
  font-weight: 700;
  line-height: 83.56px;
}

.form__title .highlighted {
  color: transparent;
  background-image: linear-gradient(to top, #c05302 0%, #da7d0f 100%);
  -webkit-background-clip: text;
}
