.form {
	max-width: 665px;
	@include cover;
	width: 100%;
	padding: 20px;
	border: 3px solid #c7a221;
	border-radius: 5px;
	&__title {
		text-align: center;
		text-shadow: 0 3px 11px #000000;
		color: #ffffff;
		font-family: $circle;
		font-size: 53px;
		font-weight: 700;
		line-height: 83.56px;
		.highlighted {
			color: transparent;
			background-image: linear-gradient(to top, #c05302 0%, #da7d0f 100%);
			-webkit-background-clip: text; } } }
